@media (max-width: 790px) {
  .map-container {
    padding: 0 24px;
  }
  .antecipar {
    flex-direction: column;
    padding: 50px 24px;
  }
 
   .differential-container {
    height: auto;
    flex-direction: column;
    display: flex;
    &:nth-child(even) {
      flex-direction: column-reverse;
    }
  }
  
  }

  .clearfix:before,
.clearfix:after {
   content: " ";
   display: table;
}

.clearfix:after {
   clear: both;
}

.clearfix {
   *zoom: 1;
}

}